<template>
  <div class="nearby">
    <h3 class="nearby__title">附近店铺</h3>
    <!-- <div
      class="nearby__item"
      v-for="(items, indexs) in nearbyList"
      :key="indexs"
    >
      <img class="nearby__item__img" :src="items.imgurl" />
      <div class="nearby__content">
        <div class="nearby__content__title">{{ items.title }}</div>
        <div class="nearby__content__tags">
          <span
            class="nearby__content__tag"
            v-for="(item, index) in items.tags"
            :key="index"
            >{{ item }}</span
          >
        </div>
        <p class="nearby__content__heightlight">
          {{ items.desc }}
        </p>
      </div>
    </div> -->
    <router-link
      v-for="(item, index) in nearbyList"
      :hideBorder="false"
      :key="index"
      :to="`/shop/${item.id}`"
    >
      <ShopInfo :items="item" />
    </router-link>
  </div>
</template>
<script>
import { get } from '@/utils/request'
import ShopInfo from '../../components/ShopInfo'
import { reactive, toRefs } from 'vue'

const useGetShopListEffect = () => {
  const data = reactive({ page: 1, limit: 10, nearbyList: {} })
  const getShopList = async () => {
    const result = await get('/api/shop/list', {
      page: data.page,
      limit: data.limit
    })
    if (result.code === 200) {
      data.nearbyList = result.data
    }
  }
  // const { page, limit, nearbyList } = toRefs(data)
  return { data, getShopList }
}
export default {
  name: 'Nearby',
  components: { ShopInfo },
  setup() {
    const { data, getShopList } = useGetShopListEffect()
    getShopList()
    const { page, limit, nearbyList } = toRefs(data)
    return { page, limit, nearbyList, getShopList }
  }
}
</script>
<style lang="scss" scoped>
@import "../../style/viriables.scss";
.nearby {
  &__title {
    margin: 0.16rem 0 0.04rem 0;
    font-size: 0.18rem;
    color: $content-fontcolor;
    font-weight: normal;
  }
  a {
    text-decoration: none;
  }
}
</style>
